import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AccountsService } from '../services/accounts.service';

@Component({
  selector: 'app-repo-details',
  templateUrl: './repo-details.component.html',
  styleUrls: ['./repo-details.component.scss']
})
export class RepoDetailsComponent implements OnInit {

  repoDetails;

  constructor(private route: ActivatedRoute, private gitHupService: AccountsService) {
    var repoName = route.snapshot.paramMap.get('repoName');
    this.gitHupService.getSingleRepo(repoName).subscribe((data) => {
      this.repoDetails=data;
      console.log(this.repoDetails);
    });
  }

  ngOnInit() {
  }



}
